.cards {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 5px;

  margin: 0;
  padding: 0;

  list-style: none;

  &__item {
    padding: 0 10px;

    border-radius: 10px;

    background-color: yellow;

    &_done {
      background-color: lawngreen;
    }
  }
}
